gallery {
	width: 640px;
	margin: 0 auto;
	padding: 5px;
	background: #fff;
	box-shadow: 0 1px 1px rgba(0,0,0,.1);
  }
  
  .gallery > div {
	position: relative;
	float: left;
	padding: 5px;
  }
  
  .gallery > div > img {
	width: 200px;
	transition: .1s transform;
	transform: translateZ(0); /* hack */
  }
  
  .gallery > div:hover {
	z-index: 1;
  }
  
  .gallery > div:hover > img {
	transform: scale(1.4,1.4);
	transition: .3s transform;
  }
  
  .cf:before, .cf:after {
	display: table;
	content: "";
	line-height: 0;
  }
  
  .cf:after {
	clear: both;
  }
  
  h1 {
	margin: 40px 0;
	font-size: 24px;
	text-align: center;
  }
  
  .RRT__container {
	position: relative;
  }
  
  /****************************/
  /******** tab styles ********/
  /****************************/
  .RRT__tabs {
	display: flex;
	flex-wrap: wrap;
  }
  
  .RRT__accordion {
	flex-direction: column;
  }
  
  .RRT__tab {
	background: #eee;
	border-style: solid;
	border-color: #ddd;
	border-width: 1px 1px 1px 0;
	cursor: pointer;
	z-index: 1;
	white-space: nowrap;
	padding: 0.7em 1em;
  }
  
  .RRT__tab:focus {
	outline: 0;
	background-color: #e6e6e6;
  }
  
  .RRT__accordion .RRT__tab {
	border-left-width: 1px;
  }
  
  .RRT__tab--first {
	border-left-width: 1px;
  }
  
  .RRT__tab--selected {
	background: #fff;
	border-color: #ddd #ddd #fff;
  }
  
  .RRT__tab--selected:focus {
	background-color: #fff;
  }
  
  .RRT__tab--disabled {
	opacity: 0.5;
	cursor: not-allowed;
  }
  
  .RRT__tab:focus {
	z-index: 2;
  }
  
  .RRT__tab--selected .RRT__removable {
	position: relative;
  }
  
  .RRT__tab--selected .RRT__removable-text {
	margin-right: 10px;
  }
  
  .RRT__tab--selected .RRT__removable-icon {
	position: absolute;
	font-size: 18px;
	right: 0.5em;
	top: 0.2em;
  }
  
  /****************************/
  /********* panel styles *****/
  /****************************/
  .RRT__panel {
	margin-top: -1px;
	padding: 1em;
  }
  
  .RRT__panel--hidden {
	display: none;
  }
  
  .RRT__accordion .RRT__panel {
	margin-top: 0;
  }
  
  /****************************/
  /******* showmore control ***/
  /****************************/
  .RRT__showmore {
	background: #eee;
	border: 1px solid #ddd;
	cursor: pointer;
	z-index: 1;
	white-space: nowrap;
	margin-left: -1px;
	position: relative;
  }
  
  .RRT__showmore--selected {
	background: white;
	border-bottom: none;
  }
  
  .RRT__showmore-label {
	padding: 0.7em 1em;
	position: relative;
	bottom: -1px;
	z-index: 1;
  }
  
  .RRT__showmore-label--selected {
	background-color: #eee;
  }
  
  .RRT__showmore-list {
	position: absolute;
	right: -1px;
	top: 100%;
	display: none;
  }
  
  .RRT__showmore-list--opened {
	display: block;
  }
  
  /****************************/
  /********** inkbar **********/
  /****************************/
  .RRT__inkbar-wrapper {
	width: 100%;
  }
  
  .RRT__inkbar {
	position: relative;
	bottom: 0;
	height: 2px;
	margin-top: -2px;
	background-color: deepskyblue;
	transition: left 800ms cubic-bezier(0.23, 1, 0.32, 1);
	z-index: 2;
  }
  
